@charset "UTF-8";

@media (min-width:320px){
  .item {
    width: -webkit-calc(50% - 8px);
  }
}
@media (min-width:700px){
  .item {
    width: -webkit-calc(33.3% - 8px);
  }
}

@media (min-width:900px){
  .item {
    width: -webkit-calc(25% - 8px);
  }
}
@media (min-width:1024px){
  .item {
    width: -webkit-calc(20% - 8px);
  }
}

.scroll {
  opacity: 0.3;
  width: 60px;
  height: 60px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  outline: none;
  border: none;
}

.scroll:active {
  opacity: 1;
}

body {
  min-width: 320px;
  font-weight:400;
  font-size:87.5%;
  font-family:'Malgun Gothic','맑은 고딕',Helvetica,'Apple SD Gothic Neo',Dotum,'돋움',sans-serif;
  color:#ececec;
  width: 100%;
  margin:0;
}

address,em,i {
  font-style:normal
}

a {
  color: #000;
  text-decoration:none
}

/**
 * Header
 */

.header {
  align-content: center;
  align-items: stretch;
  background: #3f51b5;
  box-shadow:
          0 4px 5px 0 rgba(0, 0, 0, 0.14),
          0 2px 9px 1px rgba(0, 0, 0, 0.12),
          0 4px 2px -2px rgba(0, 0, 0, 0.2);
  color: #fff;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  font-size: 20px;
  height: 56px;
  justify-content: flex-start;
  /*padding: 16px 16px 0 16px;*/
  position: fixed;
  transition: transform 0.233s cubic-bezier(0, 0, 0.21, 1) 0.1s;
  width: 100%;
  will-change: transform;
  z-index: 1000;
}

.header .sc_logo {
  flex: 1;
  height: 56px;
  width: auto;
  margin: 0;
  /*background: url(/logo.png) center center no-repeat;*/
  /*background-size: auto 40px;*/
}

.header .sc_logo img {
  margin-top: 3px;
  margin-left: 16px;
  height: 50px;
  width: auto;
}

.header button {
  border: none;
  cursor: pointer;
  height: 24px;
  margin-right: 16px;
  opacity: 0.54;
  outline: none;
  overflow: hidden;
  text-indent: -30000px;
  transition: opacity 0.333s cubic-bezier(0, 0, 0.21, 1);
  width: 24px;
}

.header .headerButton {
  margin-top: 16px;
}

