div {
  display: block;
}

.home {
  background-color: #fff;
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
  padding-top: 56px;
}

.list_area ul {
  padding-inline-start: 0px;
  list-style: none;
}

.deals {
  /*padding: 5px 0 0;*/
  background-color: #fff;

}

.item {
  margin-top: 4px;
  margin-left: 4px;
  margin-right: 4px;
  margin-bottom: 4px;
  padding: 2px 2px;
  float: left;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  height: 340px;
  /*padding: 4px;*/
}

.item li {
  display: list-item;
  text-align: -webkit-match-parent;
  list-style: none;
}

.item>a:after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 36;
  border: 1px solid rgba(22,24,26,.2);
  pointer-events: none;
  content: '';
}

.item .anchor:hover {
  text-decoration:underline
}

.item .image {
  position: relative;
}

.item .image .thumbnail {
  text-align: center;
}

.item .image .thumbnail img{
  width: 100%;
  max-width: 200px;
  height: auto;
}

.item .desc {
  height: 131px;
}

.item .desc .title {
  display:-webkit-box;
  overflow:hidden;
  height:40px;
  max-height:32px;
  padding:17px 14px 0;
  line-height:16px;
  -webkit-line-clamp:2;
  -webkit-box-orient:vertical
}

.item .desc .price_area {
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box;
  width:100%;
  padding:10px 14px;
  position: absolute;
  bottom: 23px;
  padding-top: 1px;
}

.item .desc .price_area .price_desc {
  float:left;
  font-size:14px;
  color:#f27935;
  letter-spacing:0;
  vertical-align:top
}

.item .desc .price_area .price_desc i {
  float:left;
  font-weight: 700;
}

.item .desc .price_area .original_price {
  margin-top:1px;
  margin-right:4px;
  margin-left:5px;
  font-size:12px;
  color:#c2c7cc;
  text-decoration:line-through
}

.item .desc .price_area .original_price i {
  font-style: normal;
}

.item .desc .price_area .price {
  clear: both;
  font-weight: 700;
  font-size: 15px;
  display: block;
  margin-top: 2px;
  line-height: 1;
  letter-spacing: -.06em;
}

.item .desc .price_area .price i {
  font-style: normal;
}

.carousel {
}

